/* ChatBot.css */

.chat-container {
  min-height: 200px;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10%;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.message-container {
  max-height: 300px;
  overflow-y: auto;
}

.user-message {
  background-color: #007bff;
  color: #fff;
  border-radius: 8px;
  padding: 8px;
  margin-bottom: 8px;
}

.bot-message {
  background-color: #eee;
  border-radius: 8px;
  padding: 8px;
  margin-bottom: 8px;
}

.input-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background-color: #f4f4f4;
}

.input-container input {
  flex: 1;
  padding: 8px;
  margin-right: 6px;
}

button {
    padding: 8px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.listening-button {
  
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
}

.listening-button:hover {
  background-color: #0056b3;
}

.btn{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.send{
  margin-right: 10px;
}